<template>
    <div>
        <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
                <v-btn text v-bind="attrs" v-on="on">
                    File
                </v-btn>
            </template>
            <v-list>
                <template v-for="(item, index) in fileItem.slice(0, 4)">
                    <v-divider v-if="item.divider" :key="index" :inset="item.inset"></v-divider>
                    <v-list-item v-else :key="index" @click="item.menuAction(item.title)">
                            <v-icon  left small>{{item.icon}} </v-icon> 
                            <v-list-item-content  class="font-weight-medium">
                                <v-list-item-title v-text="item.title"></v-list-item-title>
                            </v-list-item-content>
                            <v-list-item-action v-text="item.Shortcut" class="font-weight-light"></v-list-item-action>
                    </v-list-item>
                </template>
            </v-list>
        </v-menu>
        <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
                <v-btn text v-bind="attrs" v-on="on">
                    Project
                </v-btn>
            </template>
            <v-list>
                <template v-for="(item, index) in projectItem.slice(0, 6)">
                    <v-divider v-if="item.divider" :key="index" :inset="item.inset"></v-divider>
                    <v-list-item v-else :key="index" @click="item.menuAction(item.title)">
                            <v-icon  left small>{{item.icon}} </v-icon> 
                            <v-list-item-content class="font-weight-medium">
                                <v-list-item-title v-text="item.title"></v-list-item-title>
                            </v-list-item-content>
                            <v-list-item-action v-text="item.Shortcut" class="font-weight-light"></v-list-item-action>
                    </v-list-item>
                </template>
            </v-list>
        </v-menu>
        <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
                <v-btn text v-bind="attrs" v-on="on">
                    Tool
                </v-btn>
            </template>
            <v-list>
                <v-list-item v-for="(item, index) in toolItem" :key="index"  @click="item.menuAction(item.title)">
                    <v-icon  left small>{{item.icon}} </v-icon> 
                    <v-list-item-content class="font-weight-medium">
                        <v-list-item-title v-text="item.title"></v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-action v-text="item.Shortcut" class="font-weight-light"></v-list-item-action>
                </v-list-item>
            </v-list>
        </v-menu>
        <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
                <v-btn text v-bind="attrs" v-on="on">
                    Setting
                </v-btn>
            </template>
            <v-list>
                <v-list-item v-for="(item, index) in settingItem" :key="index" @click="item.menuAction(item.title)">
                    <v-icon  left small>{{item.icon}} </v-icon> 
                    <v-list-item-content class="font-weight-medium">
                        <v-list-item-title v-text="item.title"></v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-action v-text="item.Shortcut" class="font-weight-light"></v-list-item-action>
                </v-list-item>
            </v-list>
        </v-menu>
        <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
                <v-btn text v-bind="attrs" v-on="on">
                    Help
                </v-btn>
            </template>
            <v-list>
                <v-list-item v-for="(item, index) in helpItem" :key="index" @click="item.menuAction(item.title)">
                    <v-icon  left small>{{item.icon}} </v-icon> 
                    <v-list-item-content class="font-weight-medium">
                        <v-list-item-title v-text="item.title"></v-list-item-title>
                    </v-list-item-content>
                    <v-list-item-action v-text="item.Shortcut" class="font-weight-light"></v-list-item-action>
                </v-list-item>
            </v-list>
        </v-menu>
        <v-dialog v-model='dialogSetting' width="500" persistent>
            <v-card>
                <v-card-title class="text-h6 green accent-1"> Settings</v-card-title>
                <v-subheader >Main View Zoom</v-subheader>
                <v-card-text>
                    <v-slider v-model="zoomMain" append-icon="mdi-magnify-plus-outline" prepend-icon="mdi-magnify-minus-outline"
                        :max="2" :min="0.3" @click:append="zoomMainIn" @click:prepend="zoomMainOut" step="0.05" ticks="always" tick-size="3"></v-slider>
                </v-card-text>
                <!-- <v-subheader >Detail View</v-subheader>
                <v-card-text>
                    <v-switch v-model="switchDetailView" inset :label="switchDetailView ? 'visible' : 'unvisible'"></v-switch>
                </v-card-text>
                <v-subheader >Detail View Zoom</v-subheader>
                <v-card-text>
                    <v-slider v-model="zoomDetail" append-icon="mdi-magnify-plus-outline" prepend-icon="mdi-magnify-minus-outline"
                        :max="1.2" :min="0.8" @click:append="zoomDetailIn" @click:prepend="zoomDetailOut" step="0.1" ticks="always" tick-size="3"></v-slider>
                </v-card-text> -->
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn class="d-inline-flex ml-3 mr-1" color="green darken-1" text  @click="saveSetting()" >
                        Save
                    </v-btn>
                    <v-btn class="d-inline-flex ml-3 mr-1" color="green darken-1" text @click="cancelSetting()">
                        Cancel
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model='dialogAbout' width="500" persistent>
            <v-card>
                <v-card-title class="text-h6 green accent-1"> 
                    <v-icon>mdi-information-outline</v-icon>  SAHL
                </v-card-title>
                <br>
                <v-card-text>
                    <v-row >
                        <label style="margin-left: 60px;">Version : 1.0</label>
                    </v-row>
                    <v-row >
                        <label style="margin-left: 60px;">Production company : BNSsoft</label>
                    </v-row>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn class="d-inline-flex ml-3 mr-1" color="green darken-1" text @click="closeAbout()">
                        close
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialogError" persistent width="600">
            <v-card >
                <v-alert v-model="dialogError" width="600" prominent border="left" outlined type="warning" dismissible>
                    먼저 Project를 만들어 주세요.
                </v-alert>
            </v-card>
        </v-dialog>
        <dialogSave v-model="dialogSaveWindow"/>
    </div>
</template>

<script>
import dialogSave from '../components/dialogSave.vue'
import { EventBus } from '../main'

document.onkeydown = function(e) { //단축키 만들기
  //console.log(e)
  if(e.altKey  && e.keyCode == 78) { // Alt+n  => new file
    EventBus.$emit('shortcut-keys', 'newfile')
  } 
  if(e.altKey  && e.keyCode == 79) { //Alt+o => Import Arxml
    EventBus.$emit('shortcut-keys', 'importARXML')
  }
  if(e.altKey  && e.keyCode == 83) { //Alt+s => save
    EventBus.$emit('shortcut-keys', 'save')
  }
  if(e.altKey  && e.keyCode == 67) { //Alt+c => copy
    EventBus.$emit('shortcut-keys', 'copy')
  }
  if(e.ctrlKey && e.keyCode == 86 && e.shiftKey) { //ctrl+shift+v => validation check
    EventBus.$emit('shortcut-keys', 'validation')
  }
  /*if(e.ctrlKey && e.keyCode == 70 && e.shiftKey) { //ctrl+shift+f => search
    EventBus.$emit('shortcut-keys', 'search')
  }*/
  if(e.ctrlKey && e.keyCode == 81 && e.shiftKey) { //ctrl+shift+q => delete file
    EventBus.$emit('shortcut-keys', 'deletefile')
  }
  if(e.ctrlKey && e.shiftKey && e.keyCode == 69) { //ctrl+shift+e => setting
    EventBus.$emit('shortcut-keys', 'setting')
  }
  if(e.keyCode == 116) { //F5
    EventBus.$emit('shortcut-keys', 'F5')
  }
}

export default({
    components: { dialogSave},
    computed: {
        SAHLProject() {
            return this.$store.state.SAHLProject
        },
        setting() {
            return this.$store.state.setting
        },
        visibleDetailView() {
            return this.$store.state.visibleDetailView
        },
        visibleLine() {
            return this.$store.state.visibleLine
        },
        ismakeProject() {
            return this.$store.state.ismakeProject
        },
    },
    watch: {
        ismakeProject(val) { // project가 없는상태에서 다른 compoment들을 만들어 놓으니 에러가 떠서 만들어줌
            this.isprojectOpen = val
        }
    },
    data() {
        return {
            fileItem: [
                // { title: 'New Element', Shortcut:'Alt+N', icon:'mdi-file-plus', menuAction: action => { this.newFile(action) }},
                // { title: 'Copy&Paste Element', Shortcut:'Alt+C', icon:'mdi-delete', menuAction: action => { this.copy(action) }},
                // { title: 'Delete Element', Shortcut:'Ctrl+Shift+Q', icon:'mdi-delete', menuAction: action => { this.deleteFile(action) }},
                // { divider: true, inset: true},
                { title: 'Import Arxml', Shortcut:'Alt+O', icon:'mdi-open-in-app', menuAction: action => { this.importARXML(action) }},
                { title: 'Export Arxml', icon:'mdi-file-export', menuAction: action => { this.export(action) }},
                { divider: true, inset: true},
                { title: 'Exit', icon:'mdi-exit-to-app', menuAction: action => { this.exit(action) }},
            ],
            projectItem: [
                { title: 'New Project', icon:'mdi-folder-plus',  menuAction: action => { this.newProject(action) }},
                { title: 'Delete Project', icon:'mdi-delete', menuAction: action => { this.deleteProject(action) }},
                { title: 'Load Project', icon:'mdi-login-variant', menuAction: action => { this.loadProject(action) }},
                { divider: true, inset: true},
                { title: 'Save&Close Project', icon:'mdi-window-close', menuAction: action => { this.closeProject(action) }},
                { title: 'Save Project', Shortcut:'Alt+S', icon:'mdi-content-save', menuAction: action => { this.saveProject(action) }},
            ],
            toolItem: [
                { title: 'Validation', Shortcut:'Ctrl+Shift+V', icon:'mdi-play', menuAction: action => { this.setValidation(action) }},
                // { title: 'Search', Shortcut:'Ctrl+Shift+F', icon:'mdi-magnify', menuAction: action => { this.setSearch(action) }},
            ],
            settingItem: [
                { title: 'Setting', Shortcut:'Ctrl+Shift+E', icon:'mdi-cog-outline', menuAction: action => { this.setSetting(action) }},
                // { title: 'Visible Line', icon:'mdi-vector-line', menuAction: action => { this.setVisibleLine(action) }},
            ],
            helpItem: [
                { title: 'About', icon:'mdi-help-circle-outline', menuAction: action => { this.setAbout(action) }},
            ],
            dialogSetting: false,
            dialogAbout: false,
            zoomMain: this.$store.state.setting.zoomMain,
            zoomDetail: this.$store.state.setting.zoomDetail,
            dialogSaveWindow: false,
            isprojectOpen: false,
            dialogError: false,
            isOpenCloseSearch: false,
            switchDetailView: this.$store.state.visibleDetailView,
        }
    },
    mounted() {
        EventBus.$on('shortcut-keys', (str) => {
            console.log(str)
            if (str == 'newfile') {
                this.newFile()
            } else if (str == 'save') {
                this.saveProject()
            } else if (str == 'search') {
                this.setSearch()
            } else if (str == 'deletefile') {
                this.deleteFile()
            } else if (str == 'setting') {
                this.setSetting()
            } else if (str == 'importARXML') {
                this.importARXML()
            } else if (str == 'validation') {
                this.setValidation()
            } else if (str == 'F5') {
                if(this.$store.state.SAHLProject.length > 0) {
                    this.$store.commit('saveProject', {howtoClick: 1} )
                }
                this.$store.commit('setExit', )
            } else if (str == 'copy') {
                this.copy()
            }
        })
    },
    methods: {
        newProject() {
            EventBus.$emit('new-Project')
        },
        deleteProject() {
            if (this.$store.state.SAHLProject.length > 0){
                EventBus.$emit('delete-project')
            } else {
                this.dialogError = true
                setTimeout(() => {this.dialogError = false}, 4000)
            }
        },
        newFile() {
            if (this.$store.state.SAHLProject.length > 0) {
                EventBus.$emit('new-file')
            }
        },
        deleteFile() {
            if (this.$store.state.SAHLProject.length > 0) {
                EventBus.$emit('delete-file')
            }
        },
        copy() {
            if (this.$store.state.SAHLProject.length > 0) {
                EventBus.$emit('copy-file')
            }
        },
        importARXML() {
            if(this.$store.state.SAHLProject.length == 0){
                this.dialogError = true
                setTimeout(() => {this.dialogError = false}, 4000)
            } else {
                EventBus.$emit('shortcut-keys-appbar', 'ImportARXML')
            }
        },
        setValidation() {
            if(this.$store.state.SAHLProject.length == 0){
                this.dialogError = true
                setTimeout(() => {this.dialogError = false}, 4000)
            } else {
                EventBus.$emit('shortcut-keys-appbar', 'validation')
            }
        },
        setSetting() {
            this.zoomMain = this.$store.state.setting.zoomMain
            this.zoomDetail = this.$store.state.setting.zoomDetail
            this.dialogSetting = true
        },
        setSearch() {
            if(this.$store.state.SAHLProject.length == 0){
                this.dialogError = true
                setTimeout(() => {this.dialogError = false}, 4000)
            } else {
                this.isOpenCloseSearch = this.isOpenCloseSearch ? false : true
                this.$store.commit('setOpenCloseSearch', {isopen: this.isOpenCloseSearch} )
            }
        },
        export() {
            if(this.$store.state.SAHLProject.length == 0){
                this.dialogError = true
                setTimeout(() => {this.dialogError = false}, 4000)
            } else {
                this.dialogSaveWindow = true
            }
        },
        closeProject() {
            if(this.$store.state.SAHLProject.length == 0){
                this.dialogError = true
                setTimeout(() => {this.dialogError = false}, 4000)
            } else {
                this.$store.commit('saveProject', {howtoClick: 3})
            }
        },
        saveProject() {
            if(this.$store.state.SAHLProject.length == 0){
                this.dialogError = true
                setTimeout(() => {this.dialogError = false}, 4000)
            } else {
                this.$store.commit('saveProject', {howtoClick: 2} )
            }
        },
        loadProject() {
            EventBus.$emit('load-Project')
        },
        saveSetting() {
            this.$store.commit('setZoomInOut', {valueMain: this.zoomMain, valueDetail: this.zoomDetail})
            this.$store.commit('setVisibleDetailView', {visible : this.switchDetailView})
            this.$store.commit('setOpenCloseDetailView', {isopen: this.switchDetailView} )
            this.cancelSetting()
        },
        cancelSetting() {
            this.dialogSetting = false
            this.switchDetailView = this.$store.state.visibleDetailView
        },
        zoomMainOut () {
            this.zoomMain = this.zoomMain <= 0.2 ? 0.2 : this.zoomMain - 0.1
        },
        zoomMainIn () {
            this.zoomMain = this.zoomMain >= 2 ? 2 : this.zoomMain + 0.1
        },
        zoomDetailOut () {
            this.zoomDetail = this.zoomDetail <= 0.8 ? 0.8 : this.zoomDetail - 0.1
        },
        zoomDetailIn () {
            this.zoomDetail = this.zoomDetail >= 1.2 ? 1.2 : this.zoomDetail + 0.1
        },
        setVisibleLine() {
            var isVisible = this.$store.state.visibleLine ? false : true
            this.$store.commit('setVisibleLine', {isvisible: isVisible})
        },
        exit() {
            if(this.$store.state.SAHLProject.length > 0) {
                this.$store.commit('saveProject', {howtoClick: 1})
            }
            this.$store.commit('setExit', )
            window.open('about:blank','_self').self.close()
            window.close()
        },
        setAbout() {
            this.dialogAbout = true
        },
        closeAbout() {
            this.dialogAbout = false
        },

    },
})
</script>
